import React, { Component } from 'react';
import { connect } from 'react-redux';
import './HomeHeader.scss';
import coviet from '../../assets/Co-Vietnam.jpg';
import coanh from '../../assets/co-anh.png';
import { FormattedMessage } from 'react-intl';
import {LANGUAGES} from '../../utils';
import {changeLanguageApp} from '../../store/actions';

class HomeHeader extends Component {
    changeLanguage = (language) => {
        this.props.changeLanguageAppRedux(language)
        //fire redux event: actions
    }

    render() {
        console.log('check props: ', this.props)
        let language = this.props.language;
        console.log('check language: ',language)
        return (
            <React.Fragment>
            <div className='home-header-container'>
                <div className='home-header-content'>
                    <div className='left-content'>
                        <i class="fas fa-bars"></i>
                        <div className='header-logo'></div>
                    </div>
                    <div className='center-content'>
                        <div className='child-content'>
                            <div className='bold'><b><FormattedMessage id='homeheader.healthfacilities'/></b></div>
                            <div className='subs-title'><FormattedMessage id='homeheader.Choosehospitalclinic'/></div>
                        </div>
                        <div className='child-content'>
                            <div className='bold'><b><FormattedMessage id='homeheader.fee'/></b></div>
                            <div className='subs-title'><FormattedMessage id='homeheader.check-health'/></div>
                        </div>
                        <div className='child-content'>
                            <div className='bold'><b><FormattedMessage id='homeheader.speciality'/></b></div>
                            <div className='subs-title'><FormattedMessage id='homeheader.search-doctor'/></div>
                        </div>
                        <div className='child-content'>
                            <div className='bold'><b><FormattedMessage id='homeheader.doctor'/></b></div>
                            <div className='subs-title'><FormattedMessage id='homeheader.select-doctor'/></div>
                        </div>
                    </div>
                    <div className='right-content'>
                        <div className='support'><i className='fas fa-question-circle'></i><FormattedMessage id='homeheader.support'/></div>
                        <div className='language-vi'>
                            <div className='anhviet'>
                                <img src={coviet}/>
                            </div>
                            <div className={language === LANGUAGES.VI ? 'textviet active': 'textviet'}><span onClick={() => this.changeLanguage(LANGUAGES.VI)}>Vit Nam</span></div>
                        </div>
                        <div className='language-en'>
                            <div className='anhanh'><img src={coanh}/></div>
                            <div className={language === LANGUAGES.EN ? 'textanh active': 'textanh'}><span onClick={() => this.changeLanguage(LANGUAGES.EN)}>English</span></div>
                        </div>
                    </div>
                </div>
            </div>
            <div className='home-header-banner'>
                <div className='content-up'>
                    <div className='title1'><FormattedMessage id='banner.title1'/></div>
                    <div className='title2'><FormattedMessage id='banner.title2'/></div>
                    <div className='search'>
                        <i className='fas fa-search'></i>
                        <input type='text' className='change-text' placeholder='Tm chuyn khoa khm sc khe...'/>
                    </div>
                </div>
                <div className='content-down'>
                    <div className='options'>
                        <div className='option-child'>
                            <div className='icon-child'><i className="far fa-hospital"></i></div>
                            <div className='text-child'><FormattedMessage id='banner.child1'/></div>
                        </div>
                        <div className='option-child'>
                            <div className='icon-child'><i className="fas fa-mobile-alt"></i></div>
                            <div className='text-child'><FormattedMessage id='banner.child2'/></div>
                        </div>
                        <div className='option-child'>
                            <div className='icon-child'><i className="fas fa-procedures"></i></div>
                            <div className='text-child'><FormattedMessage id='banner.child3'/></div>
                        </div>
                        <div className='option-child'>
                            <div className='icon-child'><i className="fas fa-flask"></i></div>
                            <div className='text-child'><FormattedMessage id='banner.child4'/></div>
                        </div>
                        <div className='option-child'>
                            <div className='icon-child'><i className="fas fa-user-md"></i></div>
                            <div className='text-child'><FormattedMessage id='banner.child5'/></div>
                        </div>
                        <div className='option-child'>
                            <div className='icon-child'><i className="fas fa-briefcase-medical"></i></div>
                            <div className='text-child'><FormattedMessage id='banner.child6'/></div>
                        </div>
                    </div>
                </div>
                
            </div>
            </React.Fragment>
        );
    }

}

const mapStateToProps = state => {
    return {
        isLoggedIn: state.user.isLoggedIn,
        language: state.app.language,
    };
};

const mapDispatchToProps = dispatch => {
    return {
        changeLanguageAppRedux: (language) => dispatch(changeLanguageApp(language))
    };
};

export default connect(mapStateToProps, mapDispatchToProps)(HomeHeader);
